<template>
    <div class="textinformation">
<div class="pageHeader">
  <el-page-header @back="goBack" content="" >
</el-page-header>
  </div>
       <div class="input"> 
  <el-input v-model="input" placeholder=""></el-input>
  </div>
   <div class="searchtext" >
  <el-button type="info" size="mini"></el-button> 
   </div>
   <el-form>
   <ul class="news-list">
       <li 
       v-for="(item, index) in list"
       :key="index"
       class="news-item" @click="toNews(item)">
       <div class="news-media">
           <img style="width: 210px; height: 210px" class="news-thumbnail" src="../assets/textinfo/first.jpg">
           <div class="news-profile">
               <a class="title" href="">{{item.title}} </a>
               <p class="news-mark">
                   <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{item.source}}</span>
               
               </p></div>
       </div>
       <div class="media">
           <img style="width: 210px; height: 210px" class="news-thumbnail" src="../assets/textinfo/second.png">
           <div class="news-profile">
               <a class="title" href="">{{item.ktitle}} </a>
               <p class="news-mark">
                   <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{item.ksource}}</span>

               </p></div>
       </div>
       <div class="kmedia">
           <img style="width: 210px; height: 210px" class="news-thumbnail" src="../assets/textinfo/third.png">
           <div class="news-profile">
               <a class="title" href="">{{item.kktitle}} </a>
               <p class="news-mark">
                   <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{item.kksource}}</span>
               </p></div>
       </div>
       </li></ul>
</el-form>
<div class="block">
  <el-pagination
    layout="prev, pager, next"
    :total="50">
  </el-pagination>
</div>
        </div>
</template>
<script>
export default {
    methods:{
       goBack(){
  this.$router.go(-1);
       }},
       data(){
           return{
               list:[{
                   "id":0,
                   "title":" ",
                   "source":"",
                    "category":"",
                   "time":"2018-09-07",

                   "ktitle":"",
                   "ksource":"",
                   
                   "kktitle":" ",
                   "kksource":"",
                 
             
             }]
              
              
           }
       }
}
</script>
<style scoped>
.pageHeader{
  background-color:#ffc928;
  height:47px; 
  color:#333b41;
}
.input{
    max-width: 240px;
    position:relative;
    top:22px;
    left:450px;
  }
   .searchtext{
    position:relative;
    left:705px;
    top:-10px;
  }
  .news-media{
          box-sizing: border-box;
  }
  .title{
      font-size: 22px;
  }
  .news-profile{
      position:relative;
      left:220px;
      top:-170px;
  }
  .media{
      position:relative;
      top:-70px;
  }
   .kmedia{
      position:relative;
      top:-140px;
  }
  .block{
      position:relative;
      top:-170px;
      left:460px;
  }
  .news-mark{
    margin-right: 240px;
  }
  
</style>


