<template>
<div id="subjectpost">
 <div class="pageHeader">
  <el-page-header @back="goBack" content="" >
</el-page-header>
  </div>
<el-tabs type="border-card">
  <el-tab-pane label="">
    <el-form ref="form" :model="form" label-width="80px">
    <el-form-item label="">
     <el-input v-model="form.name" placeholder="WenYifei"></el-input>
   </el-form-item>
   <el-form-item label="">
     <el-input v-model="form.phonenumber" placeholder="17888886666"></el-input>
   </el-form-item>
   <el-form-item label="">
     <el-input v-model="form.email" placeholder="welcome@sina.com"></el-input>
   </el-form-item>
   <el-form-item label="">
     <el-input v-model="form.birthday" placeholder="1999/06/16"></el-input>
   </el-form-item>
   <el-form-item label="">
     <el-input v-model="form.place" placeholder="Hangzhou City,Zhejiang Province"></el-input>
   </el-form-item>
   <el-form-item>
     <el-button type="info"></el-button>
   </el-form-item>
    </el-form>
  </el-tab-pane>
  <el-tab-pane label="">
      <el-calendar v-model="value">
</el-calendar>

  </el-tab-pane>
  <el-tab-pane label="">
    <ul class="news-list">
       <li 
       v-for="(item, index) in list"
       :key="index"
       class="news-item" @click="toNews(item)">
       <div class="news-media">
           <img style="width: 180px; height: 180px" class="news-thumbnail" src="../assets/textinfo/ancientdesign.png">
           <div class="news-profile">
               <a class="title" href="">{{item.title}} </a>
               <p class="news-mark">
                   <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{item.source}}</span>
                 <br>
                   <span>{{item.ssource}}</span>
                <br>
                   <span>{{item.sssource}}</span>
               </p></div>
       </div>
       <div class="media">
           <img style="width: 180px; height: 180px" class="news-thumbnail" src="../assets/textinfo/ancienttree.png">
           <div class="news-profile">
               <a class="title" href="">{{item.ktitle}} </a>
               <p class="news-mark">
                   <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{item.ksource}}</span>
                 <br>
                   <span>{{item.kssource}}</span>
                <br>
                   <span>{{item.ksssource}}</span>
               </p></div>
       </div>
       <div class="kmedia">
           <img style="width: 180px; height: 180px" class="news-thumbnail" src="../assets/textinfo/third.png">
           <div class="news-profile">
               <a class="title" href="">{{item.kktitle}} </a>
               <p class="news-mark">
                   <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{item.kksource}}</span>
                 <br>
                   <span>{{item.kkssource}}</span>
                <br>
                   <span>{{item.kksssource}}</span>
               </p></div>
       </div>
       </li></ul>



  </el-tab-pane>
  <el-tab-pane label="">
  <el-row>
  <el-col :span="5" v-for="(o, index) in 3" :key="o" :offset="index > 0 ? 3 : 0">
    <el-card :body-style="{ padding: '0px' }">
      <img src="../assets/speciality/hotpot.jpg" class="image">
      <div style="padding: 14px;">
        <span></span>
        <div class="bottom clearfix">
         <div slot="tip" class="text">:{{price}}  :{{date}}</div>
          <!--<time class="time">{{ currentDate }}</time>-->
          <el-button type="info" class="button"></el-button>
        </div>
      </div>
    </el-card>
  </el-col>
</el-row></el-tab-pane>
  <el-tab-pane label="">
    <img style="width: 300px; height: 450px" 
    src="../assets/travel/cdc.jpg" 
     @click="goHome()" alt class>
     <img style="width: 300px; height: 450px" 
    src="../assets/travel/jn.jpg" 
     @click="goHome()" alt class>
     <img style="width: 300px; height: 450px" 
    src="../assets/travel/summer.jpg" 
     @click="goHome()" alt class>

<template>
  <div class="buone">
  <el-radio v-model="radio" label="1"></el-radio>
<div class="butwo">
  <el-radio v-model="radio" label="2"></el-radio></div>
  <div class="buthree">
  <el-radio v-model="radio" label="3"></el-radio></div></div>
</template>
<div class="up">
  <span></span>
<el-upload
  action="https://jsonplaceholder.typicode.com/posts/"
  list-type="picture-card"
  :on-preview="handlePictureCardPreview"
  :on-remove="handleRemove">
  <i class="el-icon-plus"></i>
</el-upload>
<el-dialog :visible.sync="dialogVisible">
  <img width="100%" :src="dialogImageUrl" alt="">
</el-dialog>
</div>
<div class="xin">
<el-row>
<el-button type="info" size="mini"></el-button>
</el-row></div>

  </el-tab-pane>
  <el-tab-pane label="">
    <span>:</span>
    <div style="margin: 10px 0 0 50px;"></div>
<el-input
  placeholder="366"
  v-model="input"
  :disabled="true">
</el-input>
  <span></span><br>
 <img style="width: 300px; height: 300px" 
    src="../assets/travel/money.png" 
     @click="goHome()" alt class>
 <img style="width: 300px; height: 294px" 
    src="../assets/travel/moneyy.png" 
     @click="goHome()" alt class>
  <el-button type="info" size="mini"></el-button>
  </el-tab-pane>
   <el-tab-pane label="" >
     <el-form ref="form" :model="form" label-width="80px">
  <el-form-item label="">
    <el-input v-model="form.name"></el-input>
  </el-form-item>
  <el-form-item label="">
    <el-select v-model="form.region" placeholder="">
      <el-option label="" value="shanghai"></el-option>
      <el-option label="" value="beijing"></el-option>
    </el-select>
  </el-form-item>
  <el-form-item label="">
    <el-col :span="11">
      <el-date-picker type="date" placeholder="" v-model="form.date1" style="width: 100%;"></el-date-picker>
    </el-col>
    <el-col class="line" :span="2">-</el-col>
    <el-col :span="11">
      <el-time-picker placeholder="" v-model="form.date2" style="width: 100%;"></el-time-picker>
    </el-col>
  </el-form-item>
  <el-form-item label="">
    <el-switch v-model="form.delivery"></el-switch>
  </el-form-item>
  <el-form-item label="">
    <el-checkbox-group v-model="form.type">
      <el-checkbox label="" name="type"></el-checkbox>
      <el-checkbox label="" name="type"></el-checkbox>
      <el-checkbox label="" name="type"></el-checkbox>
      <el-checkbox label="" name="type"></el-checkbox>
      <el-checkbox label="" name="type"></el-checkbox>
    </el-checkbox-group>
  </el-form-item>
   <el-form-item label="" >
    <el-input v-model="form.population"></el-input>
  </el-form-item>
  <el-form-item label="">
    <el-radio-group v-model="form.resource">
      <el-radio label=""></el-radio>
      <el-radio label=""></el-radio>
    </el-radio-group>
  </el-form-item>
  <el-form-item label="">
    <el-input type="textarea" v-model="form.desc"></el-input>
  </el-form-item>
  <el-form-item>
    <el-button type="info" @click="onSubmit"></el-button>
    <el-button></el-button>
  </el-form-item>
</el-form>
     </el-tab-pane>

  
    <el-tab-pane label="">
<div class="" id="myChart" :style="{width: '480px', height: '250px'}"></div>

      </el-tab-pane>
   
 
</el-tabs>

  </div>
</template>
  
<script>

 

  export default {
    data() {


      return {
        
        input: '',
         radio: '1',
         dialogImageUrl: '',
        dialogVisible: false,
           value: new Date(),
            list:[{
                   "id":0,
                   "title":"",
                   
                   "source":"",
                   "ssource":"",
                   "sssource":"",                  
                   "category":"",
                   "time":"2018-09-07",

                   "ktitle":"",
                   "ksource":"",
                   "kssource":"",
                   "ksssource":"",
                   
                   "kktitle":" ",
                   "kksource":"",
                   "kkssource":"",
                   "kksssource":""
             
             }],
           date:"08/27",
           price:"230",
           form: {
          name: '',
          email:'',
          phonenumber:'',
          birthday:'',
          place:'',
          region: '',
          date1: '',
          date2: '',
          delivery: false,
          type: [],
          resource: '',
          desc: '',
          population:'',
        }
      }
       
    },
    mounted(){
      this.drawLine();
    },
    methods: {
      drawLine(){
let myChart = this.$echarts.init(document.getElementById('myChart'))
myChart.setOption({
tooltip: {
        trigger: 'item',
        formatter: '{a} <br/>{b}: {c} ({d}%)'
    },
    legend: {
        orient: 'vertical',
        left: 10,
        data: ['', '', '', '', '']
    },
    series: [
        {
            name: '',
            type: 'pie',
            radius: ['50%', '70%'],
            avoidLabelOverlap: false,
            label: {
                show: false,
                position: 'center'
            },
            emphasis: {
                label: {
                    show: true,
                    fontSize: '30',
                    fontWeight: 'bold'
                }
            },
            labelLine: {
                show: false
            },
            data: [
                {value: 35, name: ''},
                {value: 30, name: ''},
                {value: 24, name: ''},
                {value: 15, name: ''},
                {value: 88, name: ''}
            ]
        }
    ]

})



      },
         handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePictureCardPreview(file) {
        this.dialogImageUrl = file.url;
        this.dialogVisible = true;
      },
      goHome(){
           var _this = this;
             _this.$router.push('/home');
      },
       goBack(){
  this.$router.go(-1);
       },
      onSubmit() {
        console.log('submit!');
      },
      
    }
  }
</script>

<style>
.xin{
   position:relative;
  left:915px;
  top:-450px;
}
.up{
  position:relative;
  left:915px;
  top:-460px;
}
.buone{
  position:relative;
  left:120px;
  top:2px;
}
.butwo{
  position:relative;
  left:300px;
  top:-23px;
}
.buthree{
  position:relative;
  left:600px;
  top:-44px;
}
 .title{
      font-size: 22px;
  }
  .news-profile{
      position:relative;
      left:220px;
      top:-170px;
  }
  .media{
      position:relative;
      top:-70px;
  }
   .kmedia{
      position:relative;
      top:-140px;
  }
 .pageHeader{
 background-color:#ffc928;
  height:47px; 
  color:#333b41;
}
 .bbutton{
      position:fixed;
     right:520px;
     top:450px;
}
 .is-selected {
    color: #1989FA;
  }
.insect{
    position:fixed;
     right:1000px;
}
.time {
    font-size: 13px;
    color: #999;
  }
  .favor{
 display: block;
    position:relative;
  }
  
  .bottom {
    margin-top: 13px;
    line-height: 12px;
  }

  .button {
    padding: 0;
    float: right;
  }

  .image {
    width: 100%;
    display: block;
  }

  .clearfix:before,
  .clearfix:after {
      display: table;
      content: "";
  }
  
  .clearfix:after {
      clear: both
  }

</style>


