<template>
<divid="login">
<divclass="pageHeader">
<el-page-headercontent="">
</el-page-header>
</div>
<el-form:model="ruleForm"status-icon:rules="rules"ref="ruleForm"label-width="100px"class="demo-ruleForm">
<el-form-itemlabel=""prop="user">
<el-inputtype="user"v-model="ruleForm.user"autocomplete="off"></el-input>
</el-form-item>
<el-form-itemlabel=""prop="password">
<el-inputtype="password"v-model="ruleForm.password"autocomplete="off"></el-input>
</el-form-item>

<el-form-item>
<el-buttontype="warning"@click="submitForm('ruleForm')"></el-button>
<el-buttontype="warning"@click="goRegister()"></el-button>
</el-form-item>
</el-form>
</div>
</template>

<script>
exportdefault{
data(){

varvalidateUser=(rule,value,callback)=>{
if(value===''){
callback(newError(''));
}
else{
if(value='admin'){
callback();
}

}
};
varvalidatePassword=(rule,value,callback)=>{
if(value===''){
callback(newError(''));
}
else{
if(value==='123456'){
callback();
}

}
};
return{
ruleForm:{
user:'',
password:''

},
rules:{
user:[
{validator:validateUser,trigger:'blur'}
],
password:[
{validator:validatePassword,trigger:'blur'}
]

}
};
},
methods:{
submitForm(formName){
this.$refs[formName].validate((valid)=>{
if(valid){
alert('!');
var_this=this;
_this.$router.push('/');
}
else{
console.log('errorsubmit!!');
returnfalse;
}
});
},
goRegister(){
var_this=this;
_this.$router.push('/register');
}
}
}
</script>

<style>
.pageHeader{
  background-color:#ffc928;
  height:47px; 
  color:#333b41;
}
</style>


